

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
//import { MDBDataTable } from "mdbreact"
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Form,
  Input,
  Table,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
  CardImg,
} from "reactstrap"

// import {
//   CButton,
//   CCard,
//   CCardHeader,
//   CCardBody,
//   CCardFooter,
//   CCol,
//   CContainer,
//   CForm,
//   CInput,
//   CInputGroup,
//   CInputGroupPrepend,
//   CInputGroupText,
//   CRow
// } from '@coreui/react'

import  { Redirect } from 'react-router-dom'

import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';

// import makeAnimated from 'react-select/animated';
import conf from '../../../conf.js';
import Confirmer from '../../../components/Confirmer.js'
import { format } from 'date-fns';
import { storage } from "../../../firebase/firebase";
// const cities = require('all-the-cities');

registerLocale('es', es)


class DetailTournoi extends Component {

  constructor(props) {
    super(props)
    this.state = {
      user:{},
      ville:"",
      ok:"",
      equipe:[],
      equipes:[],
      matchs:[],
      show_match:false,
      show_equipe:false,
      logo:""
    }
    this.handleDetail.bind(this)
    this.supprimer.bind(this)
    this.handleModifier.bind(this)
    
  }

  componentDidMount() {
   
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    var id=localStorage.getItem("idTournoi")
    let equipe=[]
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };

    fetch(conf.url+"tournoi/tournois/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data);
      $("#nom").val(data.nom);
      $("#type").val(data.typeTournoi);
      $("#annee").val(data.annee);
      $("#description").val(data.description);
      $("#prixparsrl").val(data.prixPartSrl);
      $("#prixparsa").val(data.prixPartSa);
      $("#prixparecole").val(data.prixPartEcole);
      $("#prixpme").val(data.prixPme);
      $("#datedebut").val(format(new Date(data.dateDebut),'dd/MM/yyyy'));
      $("#datefin").val(format(new Date(data.dateFin),'dd/MM/yyyy'));
      this.setState({equipes:data.equipes})
      this.setState({matchs:data.matchs})
      try{

        if(data.multimedia.file!=undefined){
          storage.ref("images")
          .child(data.multimedia.file).getDownloadURL()
          .then((url) => {
          
              this.setState({logo:url})
          
          })
          }
      }catch(error){

      }
    
     

      })

      
  }

  handleSupprimerEquipe=()=>{

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,

      },
    };
    
  
  
  fetch(conf.url+"equipe/equipes/delete/"+this.state.id, requestOptions)
  .then((response) => response.json())
    .then(data =>{console.log("enregitre avec succes vrai:",data)
     console.log("data send",data)
      this.setState({equipes:this.state.equipes.filter(element=>element.id!=this.state.id)})
      this.setState({show_equipe:false})
     
   } )
  }

  handleSupprimerMatch=()=>{

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,

      },
    };
    
  
  
  fetch(conf.url+"match/matchs/delete/"+this.state.id, requestOptions)
  .then((response) => response.json())
    .then(data =>{console.log("enregitre avec succes vrai:",data)
     console.log("data send",data)
      this.setState({matchs:this.state.matchs.filter(element=>element.id!=this.state.id)})
      this.setState({show_match:false})
     
   } )
  }


  handleDetail=(objet,id)=>{
    if(objet=="E"){
      localStorage.setItem('idEquipe',id);
      this.setState({ok: <Redirect to='/detail-equipe'/>});
    }else{
      localStorage.setItem('idMatch',id);
      this.setState({ok: <Redirect to='/detail-match'/>});
    }
    
  }
  toggledMatch=()=>{
    this.setState({show_match:!this.state.show_match})
  }

  toggledEquipe=()=>{
    this.setState({show_equipe:!this.state.show_equipe})
  }

  supprimer=(id,data)=>{
    if(data=="E"){
      this.setState({id,show_equipe:true})
    }else{
      this.setState({id,show_match:true})
    }
    
    
  }

  handleModifier=(objet,id)=>{
    if(objet=="E"){
      localStorage.setItem('idEquipe',id);
      this.setState({ok: <Redirect to='/modifier-equipe'/>});
    }else{
      localStorage.setItem('idMatch',id);
      this.setState({ok: <Redirect to='/modifier-match'/>});
    }
    
    
  }
  render() {

    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
            {this.state.ok}
            <Confirmer show={this.state.show_equipe} handleValider={this.handleSupprimerEquipe} toggle={this.toggledEquipe}/>
            <Confirmer show={this.state.show_match} handleValider={this.handleSupprimerMatch} toggle={this.toggledMatch}/>
            <Container fluid={true} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                <Card ClassName="mt-2">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Dtails du tournoi</h1>
                  {/* </CCardHeader> */}

                  <CardBody>

                    <Row 
                      className="center" 
                      style={{justifyContent:"center"}}
                      // style={{width:"150px",height:"150px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                    >
                      <div 
                        // style={{width:"150px",height:"150px",borderRadius:"50px",justifyContent:"center"}}
                        style={{width:"250px",height:"250px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                      >
                        <CardImg  src={this.state.logo} alt="Photo de tournoi" />
                      </div>
                    </Row>

                    <Row className="mt-3"> 
                      <Col md="4">
                        <label>Nom</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nom"
                          placeholder="Nom"
                          readOnly
                        />
                      </Col>

                      <Col md="4">
                        <label>Type de tounoi</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="type"
                          placeholder="Type de tounoi"
                          readOnly
                        />
                      </Col>

                      <Col md="4">
                        <label>Anne</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="annee"
                          placeholder="Anne du tounoi"
                          readOnly
                        />
                      </Col>
                      
                    </Row>
                  
                    <Row className="mt-4">
                      <Col md="12">
                        <label>Description</label>
                        <textarea  className="form-control" id="description" placeholder="Description" readOnly/>
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="3">
                        <label>Prix par SRL</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixparsrl"
                          step="5"
                          min="0"
                          placeholder="Prix par SRL"
                          readOnly
                        />
                      </Col>
                      <Col md="3">
                        <label>Prix par SA</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixparsa"
                          step="5"
                          min="0"
                          placeholder="Prix par SA"
                          readOnly
                        />
                      </Col>
                      <Col md="3">
                        <label>Prix par Ecole</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixparecole"
                          step="5"
                          min="0"
                          placeholder="Prix par Ecole"
                          readOnly
                        />
                      </Col>
                      <Col md="3">
                        <label>Prix PME</label>
                        <Input
                          className="form-control"
                          type="number"
                          id="prixpme"
                          step="5"
                          min="0"
                          placeholder="Prix PME"
                          readOnly
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6">
                        <label>Date de dbut</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="datedebut"
                          placeholder="Date de dbut"
                          readOnly
                        />
                      </Col>
                      <Col md="6">
                        <label>Date de fin</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="datefin"
                          placeholder="Date de fin"
                          readOnly
                        />
                      </Col>
                    </Row>
                    
                    
                    
                  </CardBody>
                </Card>
              </Col>


              <Col md="12" lg="12" xl="12">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col md="8">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h1 className="mb-0 font-size-18">Liste des quipes</h1>
                        </div>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-equipe"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter une nouvelle quipe
                        </Button>
                      </Col>
                    </Row>

                    <hr/>
                    <Row ClassName="mt-5">
                      <Col md="12">
                      <Table className="align-items-center table-flush" responsive>
                        <thead className="thead-light">
                          <tr>
                            <th scope="col">Nom quipe</th>
                            <th scope="col">Nom entreprise</th>
                            <th scope="col">Type entreprise</th>
                            <th scope="col">Paye</th>
                            <th scope="col">Archive</th>
                            <th scope="col">Actions</th>
                          </tr>
                        </thead>
                        <tbody>
                        {
                              this.state.equipes.map((element,idx) =>(

                                   !element.archive?
                                    <tr>
                                    <td >{element.nomEquipe}</td>
                                    <td >{element.nomEntreprise}</td>
                                    <td>{element.typeEntreprise}</td>
                                      {(element.paye)?
                                      (<td>OUI</td>):(<td>NON</td>)
                                      }
                                      {
                                      (element.archive)?(<td>OUI</td>):(<td>NON</td>)
                                      }
                                      <td>
                                      <a> <i style={{cursor:"pointer"}} onClick={this.handleModifier.bind(this,"E",element.id)} className="bx bx-pencil bx-sm text-primary "/> <i style={{cursor:"pointer"}} onClick={this.handleDetail.bind(this,"E",element.id)} className="bx bx-info-circle bx-sm text-primary "/> <i style={{cursor:"pointer"}} onClick={this.supprimer.bind(this,element.id,"E")} className="bx bx-trash  bx-sm text-danger "/> </a>
                                      </td>
                                    </tr>:null
                                  ))
                        }
                        
                        </tbody>
                      </Table>
                    
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>


              <Col md="12" lg="12" xl="12">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col md="8">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h1 className="mb-0 font-size-18">Liste des match</h1>
                        </div>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-match"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un nouveau match
                        </Button>
                      </Col>
                    </Row>

                    <hr/>
                    <Row ClassName="mt-5">
                      <Col md="12">
                      <Table className="align-items-center table-flush" responsive>
                        <thead className="thead-light">
                          <tr>
                            <th scope="col">Nom tournoi</th>
                            <th scope="col">Nom visit</th>
                            <th scope="col">Score visit</th>
                            <th scope="col">Nom visiteur</th>
                            <th scope="col">Score visiteur</th>
                            <th scope="col">Niveau</th>
                            <th scope="col">Etat</th>
                            <th scope="col">Actions</th>
                          </tr>
                        </thead>
                        <tbody>
                        {
                              this.state.matchs.map((element,idx) =>(

                                  
                                    <tr>
                                    <td >{$("#nom").val()}</td>
                                    {(element.equipeVisites.length)?(<td>{element.equipeVisites[0].nomEquipe}</td>):<td>Rien</td>}
                                    <td>{element.scoreVisite}</td>
                                    {(element.equipeVisiteurs.length)?(<td>{element.equipeVisiteurs[0].nomEquipe}</td>):<td>Rien</td>}
                                    <td>{element.scoreVisiteur}</td>
                                    <td>{element.niveauTournoi}</td>
                                    <td>{element.repousse}</td>
                                    <td>
                                      <a> <i style={{cursor:"pointer"}} onClick={this.handleModifier.bind(this,"M",element.id)} className="bx bx-pencil bx-sm text-primary "/> <i style={{cursor:"pointer"}} onClick={this.handleDetail.bind(this,"M",element.id)} className="bx bx-info-circle bx-sm text-primary "/> <i style={{cursor:"pointer"}} onClick={this.supprimer.bind(this,element.id,"M")} className="bx bx-trash  bx-sm text-danger "/> </a>
                                      </td>
                                    </tr>
                                  ))
                        }
                        
                        </tbody>
                      </Table>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>

            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default DetailTournoi

